.notifications {
  padding: 8px 16px;
  font-size: 16px;
  background-color: #d3d3d3;
  border-radius: 8px;
  margin-bottom: 20px;
}

.notifications.notifications-error {
  border: 3px solid red;
  color: red;
}

.notifications.notifications-success {
  border: 3px solid green;
  color: green;
}
